<style lang="less" scoped>
  .steps-content{
    overflow: hidden;
    margin-bottom: 20px;
  }
  h2 {
    font-size: 14px;
    color: #333;
    background: #F1F3F5;
    padding: 7px 12px;
    margin-bottom: 20px;
  }
  ul{overflow: hidden;}
  .msg-block {
    float: left;
    width: 24%;
    margin-right: 20px;
    margin-bottom: 12px;
    span {
      font-size: 12px;
      display: inline-block;
    }
    .name {
      /*width: 60px;*/
      color: #999;
      margin-right: 8px;
    }
    .content {
      color: #333;
    }
  }
</style>

<template>
  <div>
    <top-header-back title="审批流程详情">
      <el-button size="mini" type="text" :disabled="$utils.isNotEqual(pageData.status, 1)">
        <kye-confirm
          :disabled="$utils.isNotEqual(pageData.status, 1)"
          title="通过权限申请"
          content="确定通过权限申请流程？点击确定后访问权限将自动开通！"
          :callback="handlePass"
          :scope-data="{id: pageData.id}">
          <span><i class="el-icon-check"> 通过</i></span>
        </kye-confirm>
      </el-button>
      <el-button size="mini" type="text" :disabled="$utils.isNotEqual(pageData.status, 1)">
        <kye-prompt
          :disabled="$utils.isNotEqual(pageData.status, 1)"
          title="请输入驳回申请权限原因"
          :callback="handleReject"
          :scope-data="{id: pageData.id}">
          <span><i class="el-icon-close"> 驳回</i></span>
        </kye-prompt>
      </el-button>
    </top-header-back>
    <div class="page-container">
      <div class="steps-content">
        <h2 class="sub-header">申请人信息</h2>
        <ul>
          <li class="msg-block">
            <span class="name">申请人</span>
            <span class="content">{{pageData.applicant}}</span>
          </li>
          <li class="msg-block">
            <span class="name">申请人岗位</span>
            <span class="content">{{pageData.position}}</span>
          </li>
          <li class="msg-block">
            <span class="name">所属部门</span>
            <span class="content">{{pageData.department_name}}</span>
          </li>
          <li class="msg-block">
            <span class="name">备注</span>
            <span class="content">{{pageData.remark}}</span>
          </li>
        </ul>
      </div>
      <div class="steps-content">
        <h2 class="sub-header">申请内容</h2>
        <ul>
          <li class="msg-block">
            <span class="name">申请内容</span>
            <span class="content">{{pageData.content}}</span>
          </li>
          <li class="msg-block">
            <span class="name">申请角色名称</span>
            <span class="content">{{pageData.role_name}}</span>
          </li>
          <li class="msg-block">
            <span class="name">流程状态</span>
            <span class="content">{{pageData.rejected_reason}}</span>
          </li>
          <li class="msg-block">
            <span class="name">申请状态</span>
            <span class="content">{{$utils.checkStatus(pageData.status)}}</span>
          </li>
        </ul>
      </div>
      <div class="steps-user">
        <h2 class="sub-header">申请角色用户</h2>
        <el-table
          :data="tableData"
          size="mini"
          style="width: 100%">
          <el-table-column
            type="index"
            prop="index"
            label="序号"
            width="60">
          </el-table-column>
          <el-table-column
            prop="name"
            label="用户姓名">
          </el-table-column>
          <el-table-column
            prop="department_route"
            label="组织路径"
            :show-overflow-tooltip="true">
          </el-table-column>
          <el-table-column
            prop="position"
            label="工作职务">
          </el-table-column>
          <el-table-column
            label="入职时间"
            width="125">
            <template slot-scope="scope">
              {{scope.row.created_at | datetime}}
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
import TopHeaderBack from '../../components/header-back'
import api from '../../api'
export default {
  components: {
    TopHeaderBack
  },
  props: ['id'],
  data () {
    return {
      pageData: '',
      flow: [],
      tableData: [],
      applicationId: undefined
    }
  },
  created () {
    this.nit()
  },
  methods: {
    handlePass (id) {
      return api.putApprovalId(id).then(res => {
        if (res.code === 200) {
          this.nit()
          return Promise.resolve(true)
        } else {
          return Promise.resolve(false)
        }
      })
    },
    handleReject (id, content) {
      let params = {
        content: content
      }
      return api.patchApprovalId(id, params).then(res => {
        if (res.code === 200) {
          this.nit()
          return Promise.resolve(true)
        } else {
          return Promise.resolve(false)
        }
      })
    },
    nit () {
      this.applicationId = this.$route.params.id
      api.getApprovalId(this.applicationId).then(res => {
        this.pageData = res.result
        this.tableData = res.result.user
        this.flow = res.result.flow
      })
    }
  }
}
</script>



