<style lang="less" scoped>
.header-icon {
  height: 60px;
  /*padding: 20px 0;*/
  .icon-box{
    cursor: pointer;
  }
  .flag {
    width: 4px;
    height: 20px;
    background-color: #398cff;
    margin: 20px 0;
  }
  .title {
    font-size: 18px;
    line-height: 20px;
    margin-left: 10px;
    color: #333333;
    padding: 20px 0;
  }
}
.el-header {
  border-bottom: 2px solid #dadde2;
  .icon-box {
    float: left;
    margin: 10px 0;
  }
}
</style>
<template>
<el-header>
  <div class="header-icon">
    <div class="icon-box" @click="$router.go(-1)">
      <svg class="icon" aria-hidden="true"><use xlink:href="#icon-back"></use></svg>
    </div>
    <div class="fl title">{{title}}</div>
    <div class="fr link">
      <slot></slot>
    </div>
  </div>
</el-header>
</template>
<script>
export default {
  data () {
    return {}
  },
  props: {
    title: {
      type: String,
      required: true
    }
  }
}
</script>



